@font-face {
    font-family: 'Arcade';
    src: local("Arcade"), url( ../../../fonts/ArcadeLight-d196.ttf);
   
}

.container{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    .title{
        font-family: "Arcade";
        font-size: 50px;
        text-align: center;
    }
}

.game-list-container{
    transition: all 0.3s ease 0s;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    padding : 60px 0;

    .cartridge{
        margin: 10px;
        transition: all 0.3s ease 0s;
        
        .game-button-container{
            display: flex;
            justify-content: center;
            background: black;

            .game-button{
                padding: 5px 20px;
                cursor: pointer;
                text-align: center;
                margin: 2px;
            }
            
            .game-button:hover{
                color: yellow;
            }
            
            .text-medium{
                font-family: "Open-Sans", "sans-sarif";
                font-size: 16px ;
                line-height: 20px;
                font-weight: 400;
                color: #ecf0f1;
            }
        }
    }
    
    .cartridge:hover{
        transform: translateY(-3px);
    }
    

}




